#MarseillePage
{
    display: grid;
    width: 100%;
    grid-template-columns: 35vw 40vw;
    text-align: justify;
    justify-content: center;
    align-items: center;
    height: 80vh;
}

@media only screen and (max-width: 767px) {
    #MarseillePage {
        grid-template: "a" max-content
                        "b" max-content ;
    }
    #MessageComponent{
        grid-area: b;
    }
    #Map{
        grid-area: a;
    }
}

@media only screen and (min-width: 768px) {

}
