#Map
{
    background-image: url("../../Assets/Marseille_map/Marseille_Map.png");
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    margin: 0 auto;
}

#Map .PopUpBTN{
    cursor: pointer;
}


#Map .PopUpBTN:hover{
    transform: scale(1.3);
    transition: transform 0.5s;
}

@media only screen and (max-width: 767px) {
    #Map {
        width: 50vh;
        height:50vh;
        margin-top: 4vh;
        margin-bottom: 0vh;
    }

    #Map .PopUpBTN
    {
        height: 20vmin;
    }

    #CentraleMarseille
    {
        position: absolute;
        top: 12%;
        right:28%;
    }

    #MuCEM
    {
        position: absolute;
        top: 26%;
        left:33%;
    }

    #VieuxPort
    {
        position: absolute;
        top: 43%;
        left:30%;
    }

    #NotreDame
    {
        position: absolute;
        top: 52%;
        left:44%;
    }

    #Frioul
    {
        position: absolute;
        top: 47%;
        left:6%;
    }

    #OrangeVelodrome
    {
        position: absolute;
        top: 65%;
        Right:15%;
    }

    #Calanques
    {
        position: absolute;
        top: 72%;
        left:50%;
    }
}

@media only screen and (min-width: 768px) {
    #Map {
        margin-top: 3vh;
        width: 70vh;
        height: 70vh;
    }

    #Map .PopUpBTN
    {
        height: 12vmin;
    }

    #CentraleMarseille
    {
        position: absolute;
        top: 10%;
        right:30%;
    }

    #MuCEM
    {
        position: absolute;
        top: 28%;
        left:35%;
    }

    #VieuxPort
    {
        position: absolute;
        top: 41%;
        left:32%;
    }

    #NotreDame
    {
        position: absolute;
        top: 50%;
        left:44%;
    }

    #Frioul
    {
        position: absolute;
        top: 50%;
        left:8%;
    }

    #OrangeVelodrome
    {
        position: absolute;
        top: 58%;
        Right:22%;
    }

    #Calanques
    {
        position: absolute;
        top: 77%;
        left:53%;
    }

}