

#pIDMessageComponent
{
    font-family: "IBM_Light", sans-serif;
    color: black;
    margin: auto;
    grid-area: c;

}

#h1IDMessageComponent
{
    font-family: Briberra, cursive;
    color: black;
    grid-area: a;
}

.VerticalTraitMessage{
    grid-area: b;
}
#MessageComponent{
    display: grid;
    grid-template: "a a a" auto
                    "b c c" auto;
}

@media only screen and (max-width: 767px) {
    #MessageComponent{
        width:80vw;
        margin-left:auto ;
        margin-right: auto;
    }
    #pIDMessageComponent
    {
        font-size: 1.2vmax;
        padding-left: 4vw;
    }

    #h1IDMessageComponent
    {
        font-size: 3vmax;
        margin-bottom: 1.6vh;
    }
    .VerticalTraitMessage{
        height:15vh;
        width: 5px;
    }

}

@media only screen and (min-width: 768px) {
    #MessageComponent{
        width:32vw
    }
    #pIDMessageComponent
    {
        font-size: 0.9vmax;
        padding-left: 2vw;
    }

    #h1IDMessageComponent
    {
        font-size: 2vmax;
    }
    .VerticalTraitMessage{
        height:11vw;
        width: 7px;
    }

}
